.landing {
    position: relative;
    width: 100%;
    height: 100vh;
    background: #111;
    color: #fff;
    overflow: hidden;
}
.header {
    position: absolute; 
    top: 1rem;
    left: 50%;
    transform: translateX(-50%);
    &__wrap {
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 60vw;
    }
    &__left {
        display: flex;
        align-items: center;
        gap: 2rem;
        & li {
            font-size: 18px;
            
        }
    }
    @media (max-width:1200px) {
        &__wrap {
            width: 100vw;
          } 
     
      }
      @media (max-width:650px) {
        &__sm {
         display: none;
        } 
     
      }
      @media (max-width:370px) {
        &__left {
            left:36%
        }
        &__wrap {
            width: 100vw;
          } 
        .coolLink[data-text="Home"]{
         display: none;
        } 
     
      }

}

.socials {
    position: absolute;
    left:50%;
    transform: translateX(-50%);
    bottom: 10rem;
    display: flex;
    align-items: center;
    gap: 3rem;
  }